<template>
  <div class="main">
    <router-view name="header"></router-view>
    <div class="container">
      <div class="content">
        <Tag size="large" color="primary">选择文件类型</Tag>
        <br />
        <Select v-model="type" style="width:200px; margin-bottom: 20px;" @on-change="changeType()">
          <Option v-for="item in typeList" :value="item.value" :key="item.value">{{ item.label }}</Option>
        </Select>
        <br />
        <Tag size="large" color="primary">选择上传文件</Tag>
        <br />
        <Upload
          multiple
          type="drag"
          ref="upload"
          :max-size="2048"
          :before-upload="handleUpload"
          :on-success="uploadSuccess"
          :on-error="uploadError"
          :on-exceeded-size="handleMaxSize"
          :action="api+'/uploadFiles'"
          :data="data"
        >
          <div style="padding: 20px 0">
            <Icon type="ios-cloud-upload" size="52" style="color: #3399ff"></Icon>
            <p>Click or drag files here to upload</p>
          </div>
        </Upload>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      api: this.api,
      type: "",
      typeList: [],
      file: "",
      data: {}
    };
  },
  inject: ["reload"],
  created() {
    this.getTypes();
  },
  methods: {
    getTypes() {
      this.$http.responseType().then(resp => {
        if (resp.result.code === "200") {
          this.typeList = resp.result.data;
          this.type = this.typeList[0].value;
          this.data = { type: this.type };
        }
      });
    },
    changeType() {
      this.data = { type: this.type };
    },
    handleUpload(file) {
      this.file = file;
      return true;
    },
    uploadSuccess(res) {
      //上传成功
      if (res.result.code == 200) {
        this.$Message.info(res.result.msg);
      }
      this.reload();
    },
    uploadError(error) {
      this.$Message.info(error);
    },
    handleMaxSize(file) {
      this.$Notice.warning({
        title: "超出文件大小限制",
        desc: "文件 " + file.name + " 太大，不能超过 2M。"
      });
    }
  }
};
</script>

